<template>
  <div class="userUpdate">
    <div class="userUpdate-box col-12 col-md-6">
      <div class="title col-12 text-center">
        <h1>애견 미용 예약</h1>
        <b>편안하게 애견미용서비스를 받으세요.</b>
      </div>

      <div class="form-group">
        <div class="input-box" role="group">
          <label>예약일</label>
          <b-input-group>
            <b-form-datepicker
              id="example-datepicker"
              v-model="date"
              placeholder="날짜를 입력해주세요."
              :min="mindate"
              :max="maxdate"
            ></b-form-datepicker>
          </b-input-group>
          <b-form-invalid-feedback id="pw-feedback">
            비밀번호는 6~8자 영문숫자여야 합니다.
          </b-form-invalid-feedback>
          <b-form-text>예약은 1일~7후만 가능합니다. </b-form-text>
        </div>

        <div class="input-box" role="group">
          <label>예약시간</label>
          <b-form-select v-model="time">
            <!-- This slot appears above the options from 'options' prop -->

            <b-form-select-option :value="null" disabled selected>
              시간을 선택해주세요.
            </b-form-select-option>

            <!-- These options will appear after the ones from 'options' prop -->
            <b-form-select-option value="time1"
              >오전 9시~11시</b-form-select-option
            >
            <b-form-select-option value="time2"
              >오후 1시~3시</b-form-select-option
            >
            <b-form-select-option value="time3"
              >오후 4시~6시</b-form-select-option
            >
          </b-form-select>
          <b-form-text>3TIME 운영제입니다.</b-form-text>
        </div>

        <div class="input-box" role="group">
          <label>방문주소</label>
          <b-input-group>
            <b-input-group-prepend is-text>
              <b-form-checkbox switch class="mr-n2" v-model="adchk">
              </b-form-checkbox>
            </b-input-group-prepend>
            <b-form-input
              :disabled="!adchk"
              v-model="address"
              @click="goPopup"
              :class="addclass"
              readonly
            ></b-form-input>
            <b-form-invalid-feedback id="email-feedback">
              서울시만 서비스중입니다.
            </b-form-invalid-feedback>
          </b-input-group>
          <b-form-text v-if="!adchk"
            >방문 미용을 예약하시려면 입력해주세요. (서울시한정)</b-form-text
          >
        </div>

        <div class="input-box" role="group">
          <label>이메일</label>
          <b-input-group>
            <b-input-group-prepend is-text>
              <b-form-checkbox switch class="mr-n2" v-model="upEmail">
              </b-form-checkbox>
            </b-input-group-prepend>
            <b-form-input
              v-model="email"
              :state="emailvalid"
              max="30"
              :disabled="emailuq || !upEmail"
              aria-describedby="email-feedback"
              placeholder="이메일을 입력해주세요."
              trim
            ></b-form-input>
            <b-input-group-append>
              <b-button
                @click="emailuqchk"
                variant="info"
                :disabled="!(emailvalid && email != '')"
                >중복확인</b-button
              >
            </b-input-group-append>
          </b-input-group>
          <b-form-invalid-feedback id="email-feedback">
            이메일 형식이 아닙니다.
          </b-form-invalid-feedback>
          <b-form-text v-if="emailuqtry">{{
            emailuq == true
              ? "이메일 중복체크 되었습니다."
              : "이메일 중복입니다."
          }}</b-form-text>
        </div>

        <br />
        <div class="input-box" role="group">
          <button
            class="login-btn btn btn-primary col-12"
            @click="userUpdateOk"
            :disabled="!signbtn"
          >
            정보변경
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "userUpdate",
  data() {
    const max = new Date();
    max.setDate(max.getDate() + 7);
    const min = new Date();
    min.setDate(min.getDate() + 1);
    return {
      date: "",
      maxdate: max,
      mindate: min,
      addclass: "",
      time: "",
      address: "",
      adchk: false,
      badadd: false,
      emailuqtry: false,
      upPw: false,
      upPhone: false,
      upEmail: false,
    };
  },
  mounted() {
    if (!this.$store.state.isLogin) {
      //alert("로그인중이아니면 접근불가!");
      //this.$router.push("/");
    }
  },
  created() {
    this.update();
  },
  computed: {
    pwvalid() {
      return (
        /(?=.*[a-zA-Z])(?=.*\d).{6,8}/.test(this.password) ||
        this.password == ""
      );
    },
    pwchkvalid() {
      return this.password === this.passwordchk || this.passwordchk == "";
    },

    phonevalid() {
      return /[0-9]{10,11}/.test(this.phone) || this.phone == "";
    },
    emailvalid() {
      return (
        /[a-z0-9_+.-]+@([a-z0-9-]+\.)+[a-z0-9]{2,4}/.test(this.email) ||
        this.email == ""
      );
    },

    signbtn() {
      var pwbool = true;
      var emailbool = true;
      var phonebool = true;
      var trybool = this.upPw || this.upPhone || this.upEmail;
      if (this.upPw) {
        pwbool =
          this.pwvalid &&
          this.pwchkvalid &&
          this.password != "" &&
          this.passwordchk != "";
      }

      if (this.upPhone) {
        phonebool = this.phonevalid && this.phone != "";
      }

      if (this.upEmail) {
        emailbool = this.email != "" && this.emailvalid && this.emailuq;
      }

      return pwbool && emailbool && phonebool && trybool;
    },
  },
  methods: {
    goPopup() {
      window.open(
        this.$store.state.baseurl + "/pettime/jusoPopup",
        "pop",
        "width=570,height=420, scrollbars=yes, resizable=yes"
      );
    },

    jusoCallBack(roadFullAddr, siNm, sggNm) {
      if (siNm != "서울특별시") {
        alert("서울아님!");
        this.badadd = true;
        this.addclass = "is-invalid";
        return;
      } else {
        this.badadd = false;
        this.addclass = "";
      }
      this.address = roadFullAddr;
      this.sggNm = sggNm;
    },

    userUpdateOk() {
      var con = {
        headers: {
          "X-CSRF-TOKEN": this.$store.state.csrf,
        },
      };

      var form = new URLSearchParams();
      if (this.upPw && this.pwvalid && this.pwchkvalid) {
        form.append("password", this.password);
      }
      if (this.upPhone && this.phonevalid) {
        form.append("phone", this.phone);
      }
      if (this.upEmail && this.emailvalid && this.emailuq) {
        form.append("email", this.email);
      }
      form.append("name", "update");

      this.$http
        .post(this.$store.state.baseurl + "/pettime/rest/userUpdate", form, con)
        .then((res) => {
          if (res.data.cnt == 1) {
            alert("수정되었습니다.");
            this.update();
          } else {
            alert("서버 통신 오류");
          }
          this.$http
            .get(this.$store.state.baseurl + "/pettime/rest/csrf-token")
            .then((res) => {
              this.$store.commit("setCsrf", res.data);
            });
        });
    },

    // tryLogin() {
    //   alert("로그인 시도!");
    //   var con = {
    //     headers: {
    //       "X-CSRF-TOKEN": this.$store.state.csrf,
    //     },
    //   };
    //   var form = new URLSearchParams();
    //   form.append("username", this.username);
    //   form.append("password", this.password);
    //   this.$http.post(this.$store.state.baseurl+"/pettime/rest/loginAJAX", form, con).then((res) => {
    //     if (res.data.islogin) {
    //       alert("로그인되었습니다.");
    //       this.$store.state.isLogin = res.data.islogin;
    //       this.$store.state.userName = res.data.uesrname;
    //       this.$router.push("/");
    //     } else {
    //       alert(res.data.message);
    //     }
    //     this.$http.get(this.$store.state.baseurl+"/pettime/rest/csrf-token").then((res) => {
    //       this.$store.commit("setCsrf", res.data);
    //     });
    //   });
    // },
  },
};
</script>

<style>
.userUpdate .title b {
  font-weight: 300;
  color: rgb(52, 152, 219);
}
.userUpdate .userUpdate-box {
  margin: 5% auto;
}

.userUpdate .input-box {
  margin-top: 1%;
}

.userUpdate #waring.text-muted {
  color: #dc3545;
}
</style>
